				"html": "<h1>CSS Framework Bulma</h1>\n<p>Bulma is a modern CSS framework based on <a href=\"http://forum.freecodecamp.com/t/css-flexbox-tips-and-tricks\">Flexbox</a> and highly customizable using <a href=\"http://sass-lang.com/\">Sass</a> (SCSS).</p>\n<h2>Getting Started</h2>\n<p>Here is a simple HTML template which includes the latest compiled and minified CSS for the Bulma library.</p>\n<pre><code>&#x3C;!DOCTYPE html>\n&#x3C;html lang=\"en\">\n  &#x3C;head>\n    &#x3C;meta charset=\"utf-8\">\n    &#x3C;meta name=\"viewport\" content=\"width=device-width, initial-scale=1\">\n    &#x3C;title>Bulma Template&#x3C;/title>\n    &#x3C;link rel=\"stylesheet\" href=\"https://maxcdn.bootstrapcdn.com/font-awesome/4.5.0/css/font-awesome.min.css\">\n    &#x3C;link rel=\"stylesheet\" href=\"https://cdnjs.cloudflare.com/ajax/libs/bulma/0.0.23/css/bulma.min.css\">\n  &#x3C;/head>\n  &#x3C;body>\n    &#x3C;h1>Hello World&#x3C;/h1>\n    &#x3C;!-- Add all HTML Code here -->\n  &#x3C;/body>\n&#x3C;/html>\n</code></pre>\n<p>We have used a CDN in this example, but you can checkout other ways of installing Bulma <a href=\"http://bulma.io/documentation/overview/start/\">here</a>.</p>\n<h2>Learning Resources</h2>\n<ul>\n<li>The official documentation for Bulma is available <a href=\"http://bulma.io/documentation/overview/start/\">here</a>.</li>\n<li>Checkout Bulma's open source GitHub repository <a href=\"https://github.com/jgthms/bulma\">here</a>.</li>\n</ul>",
